/* ------------------------------------------------------------
   CSS VARIABLES (Light Mode)
   Warna berbasis token supaya mudah dikontrol & sinkron dengan Tailwind
------------------------------------------------------------ */
:root {
  /* Background palette */
  --bg-base: #FFFFFF;
  --bg-soft: #FFF8F0;
  --bg-warm: #FFEDD9;
  --bg-gold: #FEE6C8;

  /* Text palette */
  --text-primary: #1A0A03;
  --text-secondary: #4A1E0E;
  --text-muted: #8B4513;

  /* Border */
  --border-light: #F5E5D3;
  --border-default: #E8D4BE;
}

/* ------------------------------------------------------------
   DARK MODE VARIABLES 
   Aktif saat class ".dark" ada di <html> (via next-themes)
------------------------------------------------------------ */
.dark {
  --bg-base: #0A0503;
  --bg-soft: #1A1210;
  --bg-warm: #2A1F1A;
  --bg-gold: #3A2F28;

  --text-primary: #FFF8F0;
  --text-secondary: #FFE5CC;
  --text-muted: #D4B896;

  --border-light: #2A1F1A;
  --border-default: #3A2F28;
}


/* ------------------------------------------------------------
   BASE RESETS & SCAFFOLDING
------------------------------------------------------------ */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;            /* Smooth scroll antar section */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  font-family: 'Poppins', sans-serif; /* Default font project */
  transition: background-color 0.3s ease,
              color 0.3s ease;        /* Smooth theme switching */
}


/* ------------------------------------------------------------
   UTILITIES LAYER — custom utility classes
------------------------------------------------------------ */


/* ------------------------------------------------------------
   SVG / MARQUEE ANIMATION
   Digunakan di banner berjalan (Banner.svg)
------------------------------------------------------------ */
@keyframes marquee-right {
  0% { transform: translateX(-50%); }
  100% { transform: translateX(0%); }
}

.marquee-right {
  width: 200%;
  display: flex;
  animation: marquee-right 32s linear infinite;
}

@media (prefers-reduced-motion: reduce) {
  .marquee-blu-line {
    animation: none;
  }
}


/* ------------------------------------------------------------
   TEXT SELECTION & LINK DEFAULT STYLE
------------------------------------------------------------ */
::-moz-selection {
  background: #E57621; /* warna brand */
  color: white;
}
::selection {
  background: #E57621; /* warna brand */
  color: white;
}

a {
  color: inherit;
  text-decoration: none;
}


/* ------------------------------------------------------------
   ACCESSIBILITY — reduce motion mode
   Semua animasi dipersingkat drastis demi kenyamanan pengguna sensitif
------------------------------------------------------------ */
@media (prefers-reduced-motion: reduce) {
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
